//import request from './request'
import request from '@/utils/request'
// 获取公开题库列表
export function getRepoList(params) {
  return request({
    url: '/system/repos/list',
    method: 'get',
    params
  })
}

// 获取我的题库列表
export function getMyRepos(params) {
  return request({
    url: '/reposCollections/reposCollections/myRepos',
    method: 'get',
    params
  })
}

// 收藏题库
export function collectRepo(id) {
  return request({
    url: `/reposCollections/reposCollections/collect/${id}`,
    method: 'get'
  })
}

// 取消收藏题库
export function cancelCollectRepo(id) {
  return request({
    url: `/reposCollections/reposCollections/cancelCollect/${id}`,
    method: 'get'
  })
}

// 获取题库详情
export function getRepoInfo(id) {
  return request({
    url: `/practice/repo/info/${id}`,
    method: 'get'
  })
}

// 获取题目列表
export function getQuestions(id) {
  return request({
    url: `/practice/repo/questions/${id}`,
    method: 'get'
  })
}

// 提交答案
export function submitAnswers(data) {
  return request({
    url: '/practice/repo/submit',
    method: 'post',
    data
  })
}

// 获取试题列表
export function getTopicList(params) {
  return request({
    url: '/exam/topic/list',
    method: 'get',
    params
  })
}

// 开始练习
export function startPracticeRecord(data) {
  return request({
    url: '/practice/practiceRecords/start',
    method: 'post',
    data
  })
}

// 获取训练题目列表
export function getPracticeQuestions(recordId) {
  return request({
    url: `/practice/practiceRecords/${recordId}`,
    method: 'get'
  })
}

// 提交训练题目答案
export function submitPracticeAnswer(data) {
  return request({
    // url: '/practice/recordsTopic',
    url:'/practice/recordsTopic/puOption',
    method: 'post',
    data
  })
}

// 获取题库题型统计
export function getRepoTopicType(repoId) {
  return request({
    url: `/system/repos/getTopicType/${repoId}`,
    method: 'get'
  })
}

// 获取训练记录列表
export function getPracticeRecordList(params) {
  return request({
    url: '/practice/practiceRecords/list',
    method: 'get',
    params
  })
} 