<template>
  <div class="practice-detail">
    <!-- 返回按钮和标题 -->
    <div class="header">
      <div class="back" @click="$router.back()">
        <i class="el-icon-arrow-left"></i>
        返回
      </div>
      <div class="title">{{ repoInfo.title || '题库详情' }}</div>
    </div>

    <!-- 题库简介 -->
    <div class="intro-section">
      <div class="title">题库简介</div>
      <div class="content">{{ repoInfo.description || '暂无简介' }}</div>
    </div>

    <!-- 自由练习区域 -->
    <div class="practice-section">
      <div class="title">自由练习</div>
      <div class="practice-list">
        <div class="practice-item" @click="startPractice('sequence')">
          <div class="item-content">
            <i class="el-icon-sort"></i>
            <span>顺序练习</span>
            <div class="progress-info">
              <!-- <span>进度：1/545</span> -->
              <!-- <span>正确率：0%</span> -->
            </div>
          </div>
        </div>
        <div class="practice-item" @click="startPractice('random')">
          <div class="item-content">
            <i class="el-icon-refresh"></i>
            <span>随机练习</span>
            <div class="progress-info">
              <!-- <span>进度：1/545</span> -->
              <!-- <span>正确率：0%</span> -->
            </div>
          </div>
        </div>
        <div class="practice-item" @click="startPractice('wrong')">
          <div class="item-content">
            <i class="el-icon-warning-outline"></i>
            <span>背题模式</span>
          </div>
        </div>
      </div>
    </div>

    <!-- 题型练习区域 -->
    <div class="practice-section">
      <div class="title">题型练习</div>
      <div class="practice-list">
        <div class="practice-item" @click="startPractice('single')" v-if="topicStats.singleCount > 0">
          <div class="item-content">
            <i class="el-icon-circle-check"></i>
            <span>单选题</span>
            <div class="count">{{ topicStats.singleCount }}题</div>
            <div class="progress-info">
              <span v-if="topicStats.singleProgress">进度：{{ topicStats.singleProgress }}</span>
              <span v-if="topicStats.singleCorrectRate">正确率：{{ topicStats.singleCorrectRate }}%</span>
            </div>
          </div>
        </div>
        <div class="practice-item" @click="startPractice('multiple')" v-if="topicStats.multipleCount > 0">
          <div class="item-content">
            <i class="el-icon-document-checked"></i>
            <span>多选题</span>
            <div class="count">{{ topicStats.multipleCount }}题</div>
            <div class="progress-info">
              <span v-if="topicStats.multipleProgress">进度：{{ topicStats.multipleProgress }}</span>
              <span v-if="topicStats.multipleCorrectRate">正确率：{{ topicStats.multipleCorrectRate }}%</span>
            </div>
          </div>
        </div>
        <div class="practice-item" @click="startPractice('judge')" v-if="topicStats.judgeCount > 0">
          <div class="item-content">
            <i class="el-icon-finished"></i>
            <span>判断题</span>
            <div class="count">{{ topicStats.judgeCount }}题</div>
            <div class="progress-info">
              <span v-if="topicStats.judgeProgress">进度：{{ topicStats.judgeProgress }}</span>
              <span v-if="topicStats.judgeCorrectRate">正确率：{{ topicStats.judgeCorrectRate }}%</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getRepoInfo, startPracticeRecord, getRepoTopicType } from '@/api/practice'
import logger from '@/utils/logger'

export default {
  name: 'PracticeDetail',
  data() {
    return {
      repoInfo: this.$route.query.repoInfo ? 
        JSON.parse(this.$route.query.repoInfo) : 
        {
          title: '',
          description: '',
          totalCount: 0
        },
      topicStats: {
        singleCount: 0,
        multipleCount: 0,
        judgeCount: 0,
        singleProgress: '',
        multipleProgress: '',
        judgeProgress: '',
        singleCorrectRate: 0,
        multipleCorrectRate: 0,
        judgeCorrectRate: 0
      }
    }
  },
  methods: {
    // 获取题库详情
    async fetchRepoInfo() {
      try {
        const res = await getRepoInfo(this.$route.params.id)
        if (res.code === 200) {
          this.repoInfo = {
            ...this.repoInfo,
            ...res.data
          }
          logger.log('题库信息:', this.repoInfo)
        }
      } catch (error) {
        logger.error('获取题库详情失败:', error)
        this.$message.error('获取题库详情失败')
      }
    },
    
    // 开始练习
    async startPractice(type) {
      try {
        // 根据类型设置练习模式和题型
        const { practiceMode, topicType } = this.getPracticeMode(type)
        
        // 准备请求参数
        const params = {
          repoId: this.$route.params.id,
          practiceMode,
          topicType
        }

        // 如果是题型练习，添加 questionTypes 参数
        if (practiceMode === '2') {
          // 从题型统计数据中获取所有题型
          const questionTypes = []
          if (topicType === '1' && this.topicStats.singleCount > 0) {
            questionTypes.push('1')
          }
          if (topicType === '2' && this.topicStats.multipleCount > 0) {
            questionTypes.push('2')
          }
          if (topicType === '3' && this.topicStats.judgeCount > 0) {
            questionTypes.push('3')
          }
          params.questionTypes = questionTypes.join(',')
        }

        // 调用开始练习接口
        const res = await startPracticeRecord(params)

        if (res.code === 200) {
          // 跳转到练习页面
          this.$router.push({
            name: 'PracticeExam',
            params: { id: this.$route.params.id },
            query: { 
              type,
              title: this.repoInfo.title,
              recordId: res.data // 传递训练组ID
            }
          })
        }
      } catch (error) {
        logger.error('开始练习失败:', error)
        this.$message.error('开始练习失败')
      }
    },

    // 获取练习模式和题型
    getPracticeMode(type) {
      const modeMap = {
        'sequence': { practiceMode: '3', topicType: null },  // 顺序练习
        'random': { practiceMode: '1', topicType: null },    // 题库练习(随机)
        'wrong': { practiceMode: '4', topicType: null },     // 背题模式
        'single': { practiceMode: '2', topicType: '1' },     // 单选题练习
        'multiple': { practiceMode: '2', topicType: '2' },   // 多选题练习
        'judge': { practiceMode: '2', topicType: '3' }       // 判断题练习
      }
      return modeMap[type] || { practiceMode: '1', topicType: null }
    },

    // 获取题库题型统计
    async fetchTopicStats() {
      try {
        const res = await getRepoTopicType(this.$route.params.id)
        if (res.code === 200) {
          // 处理返回的数据
          const statsArray = res.data || []
          logger.log('题型统计数据:', statsArray)
          
          // 初始化计数
          let singleCount = 0
          let multipleCount = 0
          let judgeCount = 0

          // 遍历数组统计各题型数量
          statsArray.forEach(item => {
            switch(item.topic_type) {
              case '1': // 单选题
                singleCount = item.question_count
                break
              case '2': // 多选题
                multipleCount = item.question_count
                break
              case '3': // 判断题
                judgeCount = item.question_count
                break
            }
          })
          
          this.topicStats = {
            singleCount,
            multipleCount,
            judgeCount,
            singleProgress: '',
            multipleProgress: '',
            judgeProgress: '',
            singleCorrectRate: 0,
            multipleCorrectRate: 0,
            judgeCorrectRate: 0
          }

          logger.log('处理后的题型统计:', this.topicStats)
        }
      } catch (error) {
        logger.error('获取题型统计失败:', error)
        this.$message.error('获取题型统计失败')
      }
    }
  },
  created() {
    // 确保在组件创建时获取题型统计
    this.fetchTopicStats()
    // 如果需要，也可以获取题库详情
    // this.fetchRepoInfo()
  }
}
</script>

<style scoped>
.practice-detail {
  padding: 20px;
  background: #f5f7fa;
  min-height: calc(100vh - 60px);
}

.header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.back {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #606266;
  margin-right: 20px;
}

.back i {
  margin-right: 5px;
}

.title {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
}

.intro-section,
.practice-section {
  background: #fff;
  padding: 20px;
  border-radius: 4px;
  margin-bottom: 20px;
}

.intro-section .title,
.practice-section .title {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
  margin-bottom: 20px;
}

.practice-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.practice-item {
  background: #f5f7fa;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
  height: 120px;
}

.practice-item:hover {
  background: #ecf5ff;
  color: #409eff;
}

.item-content {
  height: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.item-content i {
  font-size: 24px;
  margin-bottom: 8px;
}

.item-content span {
  font-size: 14px;
  margin-bottom: 8px;
}

.count {
  font-size: 12px;
  color: #909399;
  margin-bottom: 8px;
}

.progress-info {
  font-size: 12px;
  color: #909399;
  display: flex;
  gap: 10px;
}
</style> 